body{
  background-color: aliceblue;
  
}

.app{
  display: flex;
  flex-direction: column;
 
}
.header{
  padding: 1rem 2rem;
  background: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 479px;
  box-sizing: border-box;
  margin: 0 auto;
  margin-bottom: 10px;
  border-radius: 5px;
  
}
.header h1 {
 
  color: rgb(66, 66, 66);
}
.header select {
  height: 30px;
  width: 140px;
  border-radius: 5px;
  border: 1px solid rgb(221, 221, 221);
}
section{
 border-radius: 5px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 1rem;
  width: 479px;
  background-color: white;
 
}
section h2{
  text-align: center;
 
}
.contents{
  margin: 0 auto;

}
.contents h4
{
 text-align: center;
}
.date{
  text-align: center;
  font-size: 20px;
}
.word{
  line-height: 0px;
  margin-bottom: 40px;
}
.contents div{
  margin-top: 30px;
}
.message >  p:nth-child(1){
  color: red;
}
.message >  p:nth-child(2){
  color: blue;
}
.message >  p:nth-child(3){
  color: black;
}
.loading{
  text-align: center;
}